import { ioCommands, Macro } from "./constants";
import {socket} from '../LoadPage'

function send(cmd: ioCommands, item: any){
    if(socket){
        socket.emit(cmd,item)
    }
}
export function sendNewMacro(macro:Macro){
        send(ioCommands.NEW_MACRO,macro) 
}
export function sendUpdatedMacro(macro:Macro){
    send(ioCommands.UPDATE_MACRO,macro)
}
export function deleteMacro(key:string){
    send(ioCommands.DELETE_MACRO,key)
}