import { AppBar, Button, Checkbox, Grid, IconButton, List, ListItem, ListItemIcon, ListItemText, makeStyles, Paper, TextField, Toolbar, Typography } from "@material-ui/core";
import { useEffect, useState } from "react";
import { ControlCommands, ControlKeys, Macro, MacroLine } from "../util/constants"

import ReoderIcon from '@material-ui/icons/Reorder';
import DeleteIcon from '@material-ui/icons/Delete'
import CloseIcon from '@material-ui/icons/Close';


const commands = () => {
  let a: string[] = []
  for (let i in ControlCommands) {
    //i.replaceAll('_',' ')
    a.push(i)
  }
  return a
}

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '25ch',
    },
  },
}));
interface props{
  handleClose: ()=>void
  saveMacro:(macro: Macro)=>void,
  macro: Macro | undefined
}
export default function MacroEditDialog(props:props) {
  const {handleClose, saveMacro, macro} = props
  const [move, setMove] = useState<number>(-1);
  const [hover, setHover]= useState<number>(-1);
  const [search, setSearch] = useState('')
  const [left, setLeft] = useState(commands())

  let r: MacroLine[] = []
  let n = ''
  let key: string = ''
  if(macro){
    r = macro.commands
    n = macro.name
    key = macro.key

  }
  const [right, setRight] = useState<MacroLine[]>(r);
  const [macroName, setMacroName] = useState(n)
  
  const classes = useStyles();
  
  const handleSave=()=>{
    saveMacro({name: macroName,commands: right, key})
    handleClose()
  }

  const handleSearch=(searchValue:string)=>{
    if(searchValue === ''){
      setLeft(commands())
      setSearch(searchValue)
      return 
    }
  
    let searchString= searchValue.toUpperCase()
    let newLeft = left.slice()
    
    newLeft = newLeft.filter(i=>{
      return i.includes(searchString)
    })
    setLeft(newLeft)
    setSearch(searchValue)

  }
  const removeItem=(index: number)=>{
    let newRight = right.slice()
    newRight.splice(index,1)
    setRight(newRight)
  }

  const handleMove=()=>{
    console.log('Right',right)
    console.log('Moving',move, 'above',hover)
    if(move < -1 || hover < -1) return 
    if(move === hover) return
    //But move on bottom
    let itemToMove = right[move]
    //let newRight = right.splice(move,1)
    let newRight = right.slice()
    newRight.splice(move,1)
    console.log('Removed move', newRight)
    
    if(hover ===right.length ){
     
      newRight.push(itemToMove)
    } else{
     // newRight.splice(move,1)

      newRight.splice(hover,0,itemToMove)
    }
    console.log('New Right',newRight)
    setRight(newRight)
    setMove(-1)
    setHover(-1)
  }

  const addToRight = (value: ControlKeys) => {
   // let rightMax=Object.values(right).length
    let newCMD: MacroLine = {cmd: value, var: '', requestVar: false}
    setRight(right=>[...right, newCMD])
    console.log(right)
  }
 
  const leftList = () => {
    return (
      //@ts-ignore
      <Paper style={{ width: '220', maxHeight: '80vh', overflow: 'auto' }}>
        <Typography> Command List</Typography>
         <TextField id="outlined-basic" autoComplete='off' label="Command Search" variant="standard" style={{width: '100%'}} value={search} onChange={(e)=>{handleSearch(e.target.value)}}/>
        <List dense component="div" role="list">
          {left.map((value) => {
            const labelId = `transfer-list-item-${value}-label`;

            return (
              <ListItem
                key={value}
                role="listitem"
                button
                onClick={() => addToRight(value as ControlKeys)}
                style={{fontSize: 12}}
              >

                <ListItemText id={labelId} primary={value} />
              </ListItem>
            );
          })}
          <ListItem style={{width:400}}> </ListItem>
          <ListItem />
        </List>
      </Paper>
    )
  }
  const getRight=()=>{
    let i = 0
    return right.map((value) => {
      return listItem(value,i++)
    })
  }
  const handleVarInput=(cmdIndex:number, v:string)=>{
    let newRight = right.slice()
    newRight[cmdIndex].var=v
    setRight(newRight)
  }
  const handleVarCheck=(cmdIndex:number)=>{
    let newRight = right.slice()
    newRight[cmdIndex].requestVar = !newRight[cmdIndex].requestVar
    setRight(newRight)
  }
  const listItem=(value: MacroLine, i: number)=>{
    const labelId = `transfer-list-item-${value.cmd+i}-label`;
    const getStyle = ()=>{
      if(i===move)
      return {background: 'gray'}
      if(i===hover)
      return {background: '#aaaaaa'}
    }
    return(
      <ListItem
      key={value.cmd+i}
      role="listitem"
      style={getStyle()}
      onMouseOver={()=>{if(move!==-1 && move !==i)setHover(i)}}
    >
      <ListItemIcon>
        <ReoderIcon onMouseDown={()=>setMove(i)} onMouseUp={()=>{handleMove()}}/>
      </ListItemIcon>
      <ListItemText id={labelId} style={{width: 350}}primary={value.cmd}/>
      <TextField style={{width:100}} value={value.var} onChange={(e)=>handleVarInput(i,e.target.value)}/> 
      <Checkbox checked={value.requestVar} onChange={()=>handleVarCheck(i)}/>
      <DeleteIcon onClick={()=>removeItem(i)}/>
    </ListItem>
    )
  }
  const placeHolderRight=()=>{
    return(
      <ListItem style={{width:600}}>
        <ListItemText id={'helperText'} primary={'Click on Commands in Left Column to Build Macro'} />
      </ListItem>
    )
  }
  const rightList = () => {
   
    return (
      //@ts-ignore
    
      <Paper  sx={{ maxHeight:'80vh', overflow: 'auto' }}>
        <List dense component="div" role="list">
          <ListItem> Selected Values</ListItem>
          {right.length< 1? placeHolderRight(): getRight()}
          <ListItem />
        </List>
      </Paper>
    )
  }

  const getGrid=()=>{
  return (
    //@ts-ignore
    <Grid container spacing={1} justifyContent="center" alignItems="top" overflow="auto" >
      <Grid item>{leftList()}</Grid>
      
      <Grid item>{rightList()}</Grid>
    </Grid>
  )}

  return(
    <div>
       <AppBar className={classes.appBar}>
          <Toolbar style={{ paddingBottom: 0, marginBottom: 0, height: 30}}>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Macro Editor {key}
            </Typography>
           
            <Button autoFocus color="inherit" type={'submit'} onClick={()=>handleSave()} disabled={right.length<1}>
              Save
            </Button>
           
          </Toolbar>
        </AppBar>
      <br/>
      <TextField id="outlined-basic" autoComplete={'off'} label="Macro Name" variant="outlined" value={macroName} onChange={(e)=>{setMacroName(e.target.value)}}/>
      
      {getGrid()}
    </div>
  )

}