import { Button,  Dialog, DialogTitle,Menu, MenuItem, TextField } from "@material-ui/core"
import {  useEffect, useRef, useState } from "react"
import { ControlKeys, HandleCommand, ioCommands, Macro, MacroLine, Macros } from "../util/constants"
import * as SocketSender from '../util/SocketSender'

import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import MacroEditDialog from "./MacroEditDialog";
import SettingsIcon from "@material-ui/icons/Settings";
import InputPopup from "./InputPopup";

interface macroInput_props {
    open: boolean
    cmd: string
    handleInput: (str: string) => void
}
function MacroInput(props: macroInput_props) {
    const { open, cmd, handleInput } = props
    const [input, setInput] = useState('')

    const handleSave = () => {
        handleInput(input)
        setInput('')

    }
    return (
        <Dialog open={open}>
            <DialogTitle>{cmd}</DialogTitle>
            <TextField value={input} onChange={(e) => { setInput(e.target.value) }}></TextField>
            <Button onClick={() => handleSave()}>Save</Button>
        </Dialog>
    )
}
interface Props {
    handleCommand: HandleCommand
    open: boolean,
    handleClose: () => void
    macros: Macros
}

export default function MacroDialog(props: Props) {
    const { open, handleClose, macros, handleCommand } = props
    const [addOpen, setAddOpen] = useState(false)
    const [deleteSelected, setDeleteSelected] = useState(false)
    const [editSelected, setEditSelected] = useState(false)
    const [configOpen, setConfigOpen] = useState(false)
    const [selectedMacro, setSelectedMacro] = useState<Macro | undefined>(undefined)
    const [varInput, setVarInput] = useState(<></>)

    const divRef = useRef(null)

    useEffect(()=>{
        if(!open){
            setEditSelected(false)
            setDeleteSelected(false)
        }
    },[open,editSelected,deleteSelected])
    const handleSaveMacro = (macro: Macro) => {
        console.log(macro)
        if (editSelected) {
            SocketSender.sendUpdatedMacro(macro)
            setEditSelected(false)
            setAddOpen(false)
        } else if (addOpen) {
            SocketSender.sendNewMacro(macro)
            setAddOpen(false)
        }

    }
    const sendCMD = async (cmd: ControlKeys, vartiable: string | undefined) => {
        handleCommand(cmd, vartiable)
        await new Promise(r => setTimeout(r, 1000))
    }
    const handleVarInput = async (line: MacroLine): Promise<MacroLine | false> => {
        return new Promise(async res => {
            const handleInput = (str: string) => {
                console.log(str)
                line.var = str
                // await sendCMD(line.cmd,str)
                setVarInput(<></>)
                res(line)
            }
            const handleClose= ()=>{
                setVarInput(<></>)
                res(false)
            }
            setVarInput(<InputPopup open={true} handleInput={handleInput} handleClose={handleClose} header='Macro Input' message={'Set Input For '+line.cmd} startText={''}/>)
        })
    }
    const handleAskInput = async(line: MacroLine): Promise<boolean>=>{
        return new Promise(async res=>{
           
            const handleInput =  (str: string)=>{
                setVarInput(<></>)
                res(false)
            }
            const handleClose= ()=>{
                setVarInput(<></>)
                res(true)
            }
            setVarInput(<InputPopup open={true} handleInput={handleInput} handleClose={handleClose} header='Macro Held' message={'Press Okay to Proceed'} startText={undefined}/>)
        })
    }
    const handleWait = async(line: MacroLine): Promise<boolean>=>{
        let t = parseInt(line.var)
        let cancle = false
            return new Promise(async res=>{
                const handleForceNext=()=>{
                    t = 0
                }
                const handleClose= ()=>{
                    t = 0
                    cancle = true
                }
                while(t>0){
                    await new Promise(r=>setTimeout(r,1))
                    t--
                    setVarInput(<InputPopup open={true} handleInput={handleForceNext} handleClose={handleClose} header={'Macro Waiting '+t} message={'Press Okay to Proceed'} startText={undefined}/>)
                }
                setVarInput(<></>)
                res(cancle)

            })
    }
    const processMacro = async (m: Macro) => {
        let cancle = false
        const prepedLines: MacroLine[] = []
        for (const cmd in m.commands) {
            if(cancle) return;
            let c = m.commands[cmd]
            if (c.requestVar) {
                let i = await handleVarInput(c)
                if(i === false){
                    cancle= true
                    return
                } else{
                prepedLines.push(i)
                }
            } else {
                prepedLines.push(c)
            }
        }
        if (cancle) return;
        for(const l in prepedLines){
            let line = prepedLines[l]
            console.log(l)
            if(cancle) return;
            if(line.cmd === 'MACRO_WAIT'){
                console.log('Waiting ',line.var)
                cancle= await handleWait(line)
                console.log('Wait Done')
            } else if(line.cmd ==='MACRO_ASK'){
                if(prepedLines[parseInt(l)+1]) 
                    cancle = await handleAskInput(prepedLines[parseInt(l)+1])
                else   
                    cancle = await handleAskInput(line)
            } else{
            await sendCMD(line.cmd, line.var)
            }
        }

        if(cancle){

        }


    }
    const handleMacroClick = (key: string) => {
        let macro = Object.assign(macros[key], {});
        console.log(macro)
        if (macro) {
            if (deleteSelected) {
                SocketSender.deleteMacro(key)
            } else if (editSelected) {
                setSelectedMacro(macro)
                setAddOpen(true)
            } else {
                processMacro(macro)

            }

        }
    }
    const handleAddClosed = () => {
        setEditSelected(false)
        setDeleteSelected(false)
        setAddOpen(false)
        setSelectedMacro(undefined)
    }
    const getMacros = () => {
        return Object.values(macros).map(macro => {
            return (
                <Button key={macro.name + 'button'} onClick={() => handleMacroClick(macro.key)}> {macro.name}{editSelected ? <EditIcon /> : deleteSelected ? <DeleteIcon /> : ''} </Button>
            )
        })
    }
    const handleConfigSelect = (command: ioCommands) => {
        switch (command) {
            case ioCommands.DELETE_MACRO:
                setDeleteSelected(!deleteSelected)
                setEditSelected(false)
                break;
            case ioCommands.UPDATE_MACRO:
                setEditSelected(!editSelected)
                setDeleteSelected(false)
                break;
            case ioCommands.NEW_MACRO:
                setAddOpen(true)
                break;
        }
        setConfigOpen(false)
    }

    const handleConfigButton = () => {
        //@ts-ignore
        divRef.current.focus()

        setConfigOpen(true)

    }
    const getMain = () => {
        return (
            <div key='main_marco_dialog'>
                <DialogTitle key='macro_title' id='macro title'>
                    Macros <Button key='macro_add' aria-label="add" ref={divRef}  onClick={(e) => handleConfigButton()}> <SettingsIcon/></Button>
                </DialogTitle>
                <div>
                    {getMacros()}
                </div>

                <Menu id='groups-menu' keepMounted anchorEl={divRef.current}
                    open={configOpen}
                    onClose={() => setConfigOpen(false)}>
                    <MenuItem>
                        <Button key='add_group' onClick={() => handleConfigSelect(ioCommands.NEW_MACRO)}>Add Macro</Button>
                    </MenuItem>
                    <MenuItem>
                        <Button key='rename_group' disabled={Object.entries(macros).length < 1 ? true : false} onClick={() => handleConfigSelect(ioCommands.UPDATE_MACRO)}>Edit Macro</Button>
                    </MenuItem>
                    <MenuItem>
                        <Button key='delete_group' disabled={Object.entries(macros).length < 1 ? true : false} onClick={() => handleConfigSelect(ioCommands.DELETE_MACRO)}>Delete Macro</Button>
                    </MenuItem>
                </Menu>
            </div>
        )
    }
    const getEditDialog = () => {
        return (
            <div key='macro_add_dialog'>
                <MacroEditDialog macro={selectedMacro} saveMacro={handleSaveMacro} handleClose={() => handleAddClosed()} />

            </div>
        )
    }
    return (
        <>
            <Dialog fullScreen={addOpen} key='macro_dialog' onClose={handleClose} open={open} >

                {addOpen ? getEditDialog() : getMain()}

            </Dialog>
            {varInput}
        </>
    )
}
